@import "../../framework/sass/utils/get-random-color";

.nav-item {
  width: 960px;
  height: 450px;
  position: absolute;
  font-size: 60px;
  color: #ffffff;
  text-align: center;
  line-height: 450px;
}

.nav-item.active {
  left: 0 !important;
  top: 0 !important;
  z-index: 1000 !important;
  width: 100vw !important;
  height: 100vh !important;
  opacity: 1 !important;
}

.nav-item.hide {
  top: 1024px !important;
  opacity: 0 !important;
}

@for $i from 1 through 5 {
  .nav-item:nth-of-type(#{$i}) {
    left: #{60 * ($i - 1) + 120}px;
    top: #{-60 * ($i - 1) + 260}px;
    background-color: getRandomColor("rgb");
    z-index: 5 - $i;
    transition: all 1s ease;
    opacity: (5 - $i + 1) * 0.2;
  }
}

html, body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

view-controller {
  display: block;
  width: 100%;
  height: calc(100% - 95px);
  > .view-controller {
    width: 100%;
    height: 100%;
  }
}

main {
  width: 100%;
  height: 100%;
  overflow: hidden;
}